import {H2, Title1} from "../styled/typography";
import {Subtitle2} from "../styled/typography";
import * as React from "react";
import styled from "styled-components";

interface LanguageProps {
    language: Language
}

interface Language {
    name: string,
    frameworks: string[]
}

const LanguageWrapper = styled.div`
  margin-bottom: 1.5rem;

  &:last-of-type {
    margin-bottom: 0;
  }
`

const Language = ({language}: LanguageProps) => {
    return (
        <LanguageWrapper>
            <Title1>{language.name}</Title1>
            <Subtitle2>- {language.frameworks.join(', ')}</Subtitle2>
        </LanguageWrapper>
    )
}

export const ProgrammingExperience = () => {
    const languages: Language[] = [
        {
            name: "Javascript",
            frameworks: ["Express", "Koa 2", "Hapi", "Gatsby", "React", "Vue",]
        },
        {
            name: "Kotlin",
            frameworks: ["Spring", "Jooq"]
        }
    ]

    return (
        <>
            <H2>Languages</H2>
            {languages.map(language => <Language key={language.name} language={language}/>)}
        </>
    )
}