import {H2} from "../styled/typography";
import {Subtitle1} from "../styled/typography";
import {Subtitle2} from "../styled/typography";
import {Title1} from "../styled/typography";
import * as React from "react";

interface WorkHistoryProps {
    workHistory: WorkHistory
}

interface WorkHistory {
    companyName: string,
    role: string,
    from: string,
    to: string
}

const WorkHistoryItem = ({workHistory}: WorkHistoryProps) => {
    return (
        <div style={{
            marginBottom: '0.5rem'
        }}>
            <Title1>{workHistory.companyName}</Title1>
            <Subtitle1>{workHistory.role}</Subtitle1>
            <Subtitle2>{workHistory.from} - {workHistory.to}</Subtitle2>
        </div>
    )
}

export const WorkHistory = () => {
    const workHistories: WorkHistory[] = [
        {
            companyName: "Riwers",
            role: 'Senior Full-Stack developer',
            from: 'Jan 2020 ',
            to: 'Present'
        },
        {
            companyName: "Accenture",
            role: 'Full-Stack developer',
            from: 'Jan 2019',
            to: 'Dec 2019'
        },
        {
            companyName: "Materna Information & Communications",
            role: 'Full-Stack developer',
            from: 'Jul 2017',
            to: 'Dec 2018'
        },
        {
            companyName: "IBM",
            role: 'Web developer',
            from: 'Jul 2015',
            to: 'Jun 2017'
        },
    ]

    return (
        <>
            <H2>Work History</H2>
            {workHistories.map(workHistory => <WorkHistoryItem key={workHistory.companyName}
                                                               workHistory={workHistory}/>)}
        </>
    )
}